import { isFunction } from './util';
import { observe } from './observe/index';
/**
 * 
 * @param {VueÁöÑÂÆû‰æã} vm 
 * initState ËØ¥ÊòéÔºö
 * ÂØπVueÁöÑÊï∞ÊçÆËøõË°åÂàùÂßãÂåñ
 * VueÁöÑÊï∞ÊçÆÊù•Ê∫êÊúâÔºödataÔºåcomputedÔºåwatchÔºåpropsÔºåinject...
 */
export function initState(vm) {
    const opts = vm.$options
    if (opts.data) {
        // ÂØπÊï∞ÊçÆËøõË°åÂ§ÑÁêÜ
        initData(vm)
    }
    // if (opts.computed) {
    //     // ÂØπÊï∞ÊçÆËøõË°åÂ§ÑÁêÜ
    //     initComputed()
    // }
    // if (opts.watch) {
    //     // ÂØπÊï∞ÊçÆËøõË°åÂ§ÑÁêÜ
    //     initWatch()
    // }
}

function initData(vm) {
    // Áî®Êà∑ËÆæÁΩÆ$ÂºÄÂ§¥ÁöÑÂ±ûÊÄßÔºåVueÂÜÖÈÉ®‰∏ç‰ºöËøõË°å‰ª£ÁêÜ
    let data = vm.$options.data
    // vue2‰∏≠Ôºå‰ºöÂ∞Üdata‰∏≠ÁöÑÊâÄÊúâÊï∞ÊçÆÔºåËøõË°åÊï∞ÊçÆÂä´ÊåÅÔºàObject.definePropertyÔºâ
    // Â¶ÇÊûúdataÊòØÂáΩÊï∞ÔºåÂèñËøîÂõûÂÄºÔºåÂê¶ÂàôÂéªdataÊú¨Ë∫´
    // Â¶ÇÊûúÊòØÊñπÊ≥ïÁöÑËØùÔºåÊàëËøôÈáåÂ∏åÊúõÊñπÊ≥ïÈáåÈù¢ÁöÑthisÈÉΩÊåáÂêëVM
    // ÈÄöËøá_dataÂØπdata Âíå vmËøõË°åÂÖ≥ËÅî
    data = vm._data = isFunction(data) ? data.call(vm) : data
    for (let key in data) {
        proxy(vm, '_data', key)
    }
    // ÂØπÊï∞ÊçÆËøõË°åËßÇÊµã
    observe(data)
}

function proxy(vm, source, key) {
    Object.defineProperty(vm, key, {
        get() {
            return vm[source][key]
        },
        set(newValue) {
            vm[source][key] = newValue
        }
    })
}