const ncname = `[a-zA-Z_][\\-\\.0-9_a-zA-Z]*`; // Ê†áÁ≠æÂêç 
const qnameCapture = `((?:${ncname}\\:)?${ncname})`; //  Áî®Êù•Ëé∑ÂèñÁöÑÊ†áÁ≠æÂêçÁöÑ matchÂêéÁöÑÁ¥¢Âºï‰∏∫1ÁöÑ
const startTagOpen = new RegExp(`^<${qnameCapture}`); // ÂåπÈÖçÂºÄÂßãÊ†áÁ≠æÁöÑ 
const endTag = new RegExp(`^<\\/${qnameCapture}[^>]*>`); // ÂåπÈÖçÈó≠ÂêàÊ†áÁ≠æÁöÑ
//           aa  =   "  xxx "  | '  xxxx '  | xxx
const attribute = /^\s*([^\s"'<>\/=]+)(?:\s*(=)\s*(?:"([^"]*)"+|'([^']*)'+|([^\s"'=<>`]+)))?/; // a=b  a="b"  a='b'
const startTagClose = /^\s*(\/?)>/; //     />   <div/>
// const defaultTagRE = /\{\{((?:.|\r?\n)+?)\}\}/g; // {{aaaaa}}


// ast (ËØ≠Ê≥ïÂ±ÇÈù¢ÁöÑÊèèËø∞ js css html) vdom ÔºàdomËäÇÁÇπÔºâ

// htmlÂ≠óÁ¨¶‰∏≤Ëß£ÊûêÊàê ÂØπÂ∫îÁöÑËÑöÊú¨Êù•Ëß¶Âèë tokens  <div id="app"> {{name}}</div>
// Â∞ÜËß£ÊûêÂêéÁöÑÁªìÊûú ÁªÑË£ÖÊàê‰∏Ä‰∏™Ê†ëÁªìÊûÑ  Ê†à
function createAstElement(tagName, attrs) {
    return {
        tag: tagName,
        type: 1,
        children: [],
        parent: null,
        attrs
    }
}
let root = null;
let stack = [];
function start(tagName, attributes) {
    let parent = stack[stack.length - 1];
    let element = createAstElement(tagName, attributes);
    if (!root) {
        root = element;
    }
    if (parent) {
        element.parent = parent;// ÂΩìÊîæÂÖ•Ê†à‰∏≠Êó∂ ÁªßÁª≠Áà∂‰∫≤ÊòØË∞Å
        parent.children.push(element)
    }
    stack.push(element);
}
function end(tagName) {
    let last = stack.pop();
    if (last.tag !== tagName) {
        throw new Error('Ê†áÁ≠æÊúâËØØ');
    }
}
function chars(text) {
    text = text.replace(/\s/g, "");
    let parent = stack[stack.length - 1];
    if (text) {
        parent.children.push({
            type: 3,
            text
        })
    }
}

export function parserHTML(html) {
    function advance(len) {
        html = html.substring(len);
    }

    function parseStartTag() {
        const start = html.match(startTagOpen);
        if (start) {
            const match = {
                tagName: start[1],
                attrs: []
            }
            advance(start[0].length);
            let end;
            // Â¶ÇÊûúÊ≤°ÊúâÈÅáÂà∞Ê†áÁ≠æÁªìÂ∞æÂ∞±‰∏çÂÅúÁöÑËß£Êûê
            let attr;

            while (!(end = html.match(startTagClose)) && (attr = html.match(attribute))) {
                match.attrs.push({ name: attr[1], value: attr[3] || attr[4] || attr[5] })
                advance(attr[0].length)
            }
            if (end) {
                advance(end[0].length);
            }
            return match;
        }
        return false; // ‰∏çÊòØÂºÄÂßãÊ†áÁ≠æ
    }
    while (html) { // ÁúãË¶ÅËß£ÊûêÁöÑÂÜÖÂÆπÊòØÂê¶Â≠òÂú®ÔºåÂ¶ÇÊûúÂ≠òÂú®Â∞±‰∏çÂÅúÁöÑËß£Êûê
        let textEnd = html.indexOf('<'); // ÂΩìÂâçËß£ÊûêÁöÑÂºÄÂ§¥  
        if (textEnd == 0) {
            const startTagMatch = parseStartTag(html); // Ëß£ÊûêÂºÄÂßãÊ†áÁ≠æ
            if (startTagMatch) {
                start(startTagMatch.tagName, startTagMatch.attrs)
                continue;
            }
            const endTagMatch = html.match(endTag);
            if (endTagMatch) {
                end(endTagMatch[1]);
                advance(endTagMatch[0].length);
                continue;
            }
        }
        let text; // //  </div>
        if (textEnd > 0) {
            text = html.substring(0, textEnd)
        }
        if (text) {
            chars(text);
            advance(text.length);
        }
    }
    return root;
}


