
import { patch } from './vdom/patch';
import Watcher from './observe/watcher';
export function lifecycleMixin(Vue) {
    Vue.prototype._update = function (vnode) {
        // console.log('vnode22', vnode)
        // Âç≥ÊúâÂàùÂßãÂåñÔºåÂèàÊúâÊõ¥Êñ∞
        // ÊØîËæÉÂâçÂêéÁöÑËôöÊãüËäÇÁÇπÁöÑÂ∑ÆÂºÇ
        const vm = this
        // Â∞ÜÊñ∞ÁöÑËäÇÁÇπÊõøÊç¢ÊéâËÄÅÁöÑËäÇÁÇπ
        vm.$el = patch(vm.$el, vnode)
    }
}

export function mountComponent(vm, el) {
    // Êï∞ÊçÆÂèòÂåñÂêéÔºå‰ºöÂÜçÊ¨°Ë∞ÉÁî®Êõ¥Êñ∞ÂáΩÊï∞
    let updateComponent = () => {
        // 1. ÈÄöËøárenderÁîüÊàêËôöÊãüdom
        vm._update(vm._render()) // ÂêéÁª≠Êõ¥Êñ∞ÂèØ‰ª•Ë∞ÉÂä®updateComponentÊñπÊ≥ï
        // 2. ËôöÊãüDomÁîüÊàêÁúüÂÆûDom
    }
    // ËßÇÂØüËÄÖÊ®°ÂºèÔºöÂ±ûÊÄßÊòØË¢´ËßÇÂØüËÄÖ Âà∑Êñ∞È°µÈù¢ÔºöËßÇÂØüËÄÖ
    // updateComponent()
    // Â¶ÇÊûúÂ±ûÊÄßÂèëÁîüÂèòÂåñÔºåÂ∞±Ë∞ÉÁî®updateComponentÊñπÊ≥ï
    // ÊØè‰∏Ä‰∏™ÁªÑ‰ª∂ÈÉΩÊúâ‰∏Ä‰∏™watcher
    new Watcher(vm, updateComponent, () => {
        // console.log('ÊàëÊõ¥Êñ∞ËßÜÂõæ‰∫Ü')
        // true ÂëäËØâ‰ªñÊòØ‰∏Ä‰∏™Ê∏≤ÊüìËøáÁ®ã
        // ÂêéÁª≠ËøòÊúâÂÖ∂‰ªñÁöÑwatcher
    }, true)
}