// ‰∏Ä‰∏™Â±ûÊÄßÂØπÂ∫î‰∏Ä‰∏™depÔºåÂÅöÂ±ûÊÄßÊî∂ÈõÜ
let id = 0
class Dep {
    // ÊØè‰∏Ä‰∏™Â±ûÊÄßÈÉΩÂàÜÈÖç‰∏Ä‰∏™DepÔºåÊØè‰∏Ä‰∏™DepÂèØ‰ª•Â≠òÊîæwatcherÔºåwatch‰∏≠Ë¶ÅÂ≠òÊîæDep
    constructor() {
        this.id = id++
        this.subs = [] // Áî®Êù•Â≠òÊîæwatcher
    }
    depend() {
        // Dep.target depÈáåÈù¢Ë¶ÅÂ≠òÊîæËøô‰∏™watcher watcher‰∏ÄÊ†∑Ë¶ÅÂ≠òÊîædep
        if (Dep.target) {
            // ÊäädepÁªôwatcherÔºåËÆ©watcherÂ≠òÊîædep
            Dep.target.addDep(this)
        }
    }
    addSub(watcher) {
        this.subs.push(watcher)
    }
    notify() {
        this.subs.forEach(watcher => {
            watcher.update()
        })
    }
}
Dep.target = null
export function pushTarget(watcher) {
    Dep.target = watcher
}

export function popTarget() {
    Dep.target = null
}

export default Dep