let oldArrayPrototype = Array.prototype
export let arrayMethods = Object.create(oldArrayPrototype)

// arrayMethods.__proto__ = Array.prototype ÁªßÊâø

let methods = [
    'shift',
    'unshift',
    'pop',
    'push',
    'splice',
    'reverse',
    'sort'
]

// ÊîπÂÜôÁöÑÁî®Ëá™Â∑±ÁöÑÔºåÊ≤°ÊúâÊîπÂÜôÁöÑÁî®ÂéüÊù•ÁöÑ„ÄÇ
methods.forEach(method => {
    arrayMethods[method] = function (...args) {
        // ËøôÈáåÁöÑargsÊòØ‰º†ÂÖ•ÁöÑÂèÇÊï∞ÂàóË°®
        // ÊØîÂ¶Çarr.push(1,2,3),Âàô ...args = [1, 2, 3]
        oldArrayPrototype[method].call(this, ...args)
        let inserted;
        let ob = this.__ob__;
        switch (method) {
            case 'push':
            case 'unshift':
                inserted = args;
                break;
            case 'splice':
                inserted = args.slice(2);
            default:
                break;
        }
        if (inserted) {
            // Â¶ÇÊûúÊúâÊñ∞Â¢ûÁöÑÂÄºÔºåÂ∞±ÁªßÁª≠Âä´ÊåÅÔºåËøôÈáåË¶ÅËßÇÊµãÁöÑÊòØÊï∞ÁªÑÁöÑÊØè‰∏ÄÈ°π
            ob.observeArray(inserted)
        }
    }
})
