import { initState } from "./state"
import { compileToFunction } from './compiler/index';
import { mountComponent } from './lifecycle';

// Ë°®Á§∫Âú®vueÁöÑÂü∫Á°Ä‰∏äÂÅö‰∏ÄÊ¨°Ê∑∑ÂêàÊìç‰Ωú
export function initMixin(Vue) {
    Vue.prototype._init = function (options) {
        // ËøôÈáåÁöÑthisÂ∞±ÊòØÂΩìÂâçÁöÑVueÂÆû‰æã
        const vm = this
        // Áªô$optionsËµãÂÄº
        vm.$options = options
        initState(vm)

        if (vm.$options.el) {
            // Â∞ÜÊï∞ÊçÆÊåÇËΩΩÂà∞Ëøô‰∏™Ê®°Êùø‰∏ä
            vm.$mount(vm.$options.el)
        }
    }

    Vue.prototype.$mount = function (el) {
        const vm = this
        const options = vm.$options
        el = document.querySelector(el)
        vm.$el = el
        // 1. ÊääÊ®°ÊùøÂèòÊàêÊ∏≤ÊüìÂáΩÊï∞ => ËôöÊãüDom => vnode => diff Êõ¥Êñ∞ËôöÊãüDomÔºåÊúÄÂêé‰∫ßÁîüÁúüÂÆûËäÇÁÇπÔºå‰∏ÄÊ¨°Êõ¥Êñ∞
        if (!options.render) {
            // Ëøô‰∏™renderÊòØÁî®Êà∑Ëá™Â∑±ÂÜôÁöÑrender
            let template = options.template
            if (!template && el) {
                // Áî®Êà∑Ê≤°ÊúâËá™Â∑±ÂÜôtemplateÔºåÂ∞±ÂèñelÁöÑÂÜÖÂÆπ‰Ωú‰∏∫Ê®°Êùø
                template = el.outerHTML
                let render = compileToFunction(template)
                options.render = render
            }
        }
        mountComponent(vm, el)
    }
}