import { isObject } from "../util"
import { arrayMethods } from './array';
import Dep from './dep';

class Observer {
    constructor(data) {
        // ÁªôÂΩìÂâçÁöÑÊï∞ÁªÑÊ∑ªÂä†‰∏Ä‰∏™Â±ûÊÄßÔºåÂΩìÂâçÁöÑobserverÁöÑÂÆû‰æã
        // data.__ob__ = this
        // ËøôÈáåÁî®__ob__‰ºöÂá∫Áé∞Ê≠ªÂæ™ÁéØÔºåÂõ†‰∏∫‰ºö‰∏çÂÅúÁöÑÊâßË°ådefineReactive
        Object.defineProperty(data, '__ob__', {
            value: this,
            enumerable: false // ‰∏çÂèØÊûö‰∏æ
        })

        // ÂØπÂØπË±°‰∏≠ÁöÑÊâÄÊúâÂ±ûÊÄßËøõË°åÂä´ÊåÅ
        if (Array.isArray(data)) {
            // ÂàáÁâáÁºñÁ®ãÔºàÈ´òÈò∂ÂáΩÊï∞ÔºâÔºåÊîπÂÜôÂéüÊù•ÁöÑÊï∞ÁªÑ
            // Áî®Êà∑Ë∞ÉÁî®Êó∂ÔºöÂ¶ÇÊûúË∞ÉÁî®ÁöÑÊòØ‚ÄúÂä´ÊåÅ‚ÄùÁöÑ‰∏É‰∏™ÂèòÂºÇÊñπÊ≥ïÔºåÂ∞±Áî®Ëá™Â∑±ÈáçÂÜôÁöÑÔºåÂê¶ÂàôË∞ÉÁî®ÂéüÊù•ÁöÑ„ÄÇ
            data.__proto__ = arrayMethods
            // Â¶ÇÊûúÊï∞ÁªÑÈáåÈù¢ÊòØÂØπË±°ÁöÑËØùÔºåÈúÄË¶ÅÂØπÂØπË±°ËøõË°åÁõëÊéß
            this.observeArray(data)
        } else {
            this.walk(data)
        }
    }

    observeArray(data) {
        // ÈÅçÂéÜdata
        data.forEach(item => {
            // ÂØπitemËøõË°åËßÇÊµã
            // ÂØπÊï∞ÁªÑÈáåÈù¢ÁöÑÊï∞ÁªÑÂíåÂØπË±°ËøõË°åÁõëÊéß
            observe(item)
        })
    }
    walk(data) {
        Object.keys(data).forEach(key => {
            // ÊääÂØπË±°‰∏≠ÁöÑÊâÄÊúâÁöÑÂ±ûÊÄßÂÆö‰πâÊàêÂìçÂ∫îÂºèÁöÑ
            defineReactive(data, key, data[key])
        })
    }
}

// vue2‰∏∫‰ªÄ‰πàÊÄßËÉΩ‰ΩéÔºü
// Âõ†‰∏∫Vue2‰ºöÂØπÂØπË±°ËøõË°åÈÅçÂéÜÔºåÂ∞ÜÊØè‰∏™Â±ûÊÄßÁî®definePropertyÈáçÊñ∞ÂÆö‰πâÔºàÂÖ®ÈáèÂä´ÊåÅÔºâ
function defineReactive(data, key, value) {
    // value ÊúâÂèØËÉΩÊòØÂØπË±°ÔºàÂØπË±°Â•óÂØπË±°ÔºâÔºåÈÄíÂΩíÂä´ÊåÅ
    observe(value)
    let dep = new Dep()
    Object.defineProperty(data, key, {
        get() {
            console.log('key', key)
            // ÂèñÂÄºÊó∂ÂÄôÊàëÂ∏åÊúõÂ∞ÜwatcherÂíåDepÂÖ≥ËÅîËµ∑Êù•
            // ‰ΩÜÊòØËøôÈáåÊ≤°Êúâwatcher
            if (Dep.target) {
                // ËØ¥ÊòéËøô‰∏™getÊòØÂú®Ê®°Êùø‰∏≠‰ΩøÁî®ÁöÑ
                // ËÆ©depËÆ∞‰ΩèwatcherÔºå‰æùËµñÊî∂ÈõÜ,ÂÆÉÊòØ‰∏Ä‰∏™‰æùËµñÊî∂ÈõÜÂô® 
                dep.depend()
            }
            return value
        },
        set(newV) {
            if (newV !== value) {
                observe(newV)
                // Â¶ÇÊûúÁî®Êà∑ËµãÂÄºÁöÑÊòØ‰∏Ä‰∏™Êñ∞ÂØπË±°ÔºåÈúÄË¶ÅÂ∞ÜËøô‰∏™ÂØπË±°ËøõË°åÂä´ÊåÅ
                value = newV
                dep.notify() // ÈÄöÁü•ÂΩìÂâçÁöÑÂ±ûÊÄßÂ≠òÊîæÁöÑwatcherÊâßË°å
            }

        }
    })
}

export function observe(data) {
    if (!isObject(data)) {
        return
    }
    if (data.__ob__) {
        return
    }

    return new Observer(data)
}