export function patch(oldVnode, vnode) {
    console.log('oldVnode', oldVnode.nodeType)
    if (oldVnode.nodeType == 1) {
        // console.log('ÁúüÂÆûÂÖÉÁ¥†', vnode)
        // Áî®Vnode ÊõøÊç¢ÁúüÂÆû Dom
        const parentEl = oldVnode.parentNode;
        let elm = createElm(vnode)
        // console.log()
        // Á¨¨‰∏ÄÊ¨°Ê∏≤ÊüìÂêéÔºåÂ∞±‰ºöÂà†Èô§oldVnode
        parentEl.insertBefore(elm, oldVnode.nextSibling)

        parentEl.removeChild(oldVnode)
        return elm
    }
}

function createElm(vnode) {
    let { tag, data, children, text, vm } = vnode
    if (typeof tag === 'string') {
        // ËôöÊãüËäÇÁÇπ‰ºöÊúâ‰∏Ä‰∏™elÂ±ûÊÄßÔºåÂØπÂ∫îÁúüÂÆûËäÇÁÇπ
        vnode.el = document.createElement(tag)
        children.forEach(child => {
            vnode.el.appendChild(createElm(child))
        })
    } else {
        vnode.el = document.createTextNode(text)
    }
    return vnode.el

}